<template>
<div>
  <br>
  <router-link to="/contacts/"></router-link>
  <h1 class="title"></h1>
  <button @click="goBack(id); keyInc++" :disabled="goBackDisabled" class="btn btn-dark"></button>
  <hr>
  <ul class="list-group">
    <!--    -->
    <app-contact-field
     v-for="(item, index) in contactInfo"
     :key="index + keyInc"
     :info="item"
     @edit="edit(index, $event)"
     @remove="remove(index)"></app-contact-field>
  </ul>
  <hr>
  <form @submit.prevent="submit">
      <div class="input-group flex-nowrap">
        <div class="input-group-prepend">
          <button class="btn btn-success" :disabled="!isCorrect"><i class="fas fa-plus"></i></button>
        </div>
        <input type="text" class="form-control" v-model="newField.title" placeholder="">
        <input type="text" class="form-control" v-model="newField.value" placeholder="">
      </div>
    </form>
</div>
</template>

<script>
import {
  mapActions,
  mapGetters
} from 'vuex';

import AppContactField from "./ContactField";
export default {
  components: {
    AppContactField
  },
  data() {
    return {
      newField: {
        title: '',
        value: ''
      },
      //    key  .    ,       
      keyInc: 0
    }
  },
  computed: {
    id() {
      //  ID
      return +this.$route.params.id
    },
    contactInfo() {
      //   
      return this.list[this.id].info;
    },
    goBackDisabled(){
      //     
      return this.list[this.id].infoPrev.length == 0;
    },
    ...mapGetters(['list']),
    isCorrect() {
      //    
      return /.+/.test(this.newField.title) && /.+/.test(this.newField.value)
    }
  },
  methods: {
    ...mapActions(['addField', 'removeField', 'editField', 'goBack']),
    submit() {
      this.addField({
        id: this.id,
        value: {
          title: this.newField.title,
          value: this.newField.value
        }
      })
      //  
      this.newField.title = '';
      this.newField.value = '';
    },
    edit(index, value) {
      this.editField({
        contactId: this.id,
        fieldId: index,
        value: value
      });
    },
    remove(index) {
      //  
      if (confirm(" ,     ?")) {
        this.removeField({
          contactId: this.id,
          fieldId: index
        });
      }
    }
  }
}
</script>



